@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem;
    color: $colorTexto;
    margin: 0 auto;
    font-size: 1.8rem;
    font-weight: 400;
    display: block;
    font-weight: 700;
    text-align: center;
    transition: background-color;
    transition-duration: 300ms;
    border: none;
    border-radius: 1rem;
    width: 90%;
    cursor: pointer;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken($bgColor, 10%);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}


@mixin headingBorder{
    display: inline-block;
    position: relative;

    @include tablet { 
        margin-top: 0;
    }

    &::before {
        content: '';
        background-color: v.$secundario;
        height: .5rem;
        width: 4rem;
        display: block;
        position: absolute;
        bottom: 0;
        left: 2rem;
    }
    
    &::after {
        content: '';
        background-color: v.$gris;
        height: .5rem;
        width: 2rem;
        display: block;
    }
}