@use 'base/variables' as v;
@use 'base/mixins' as m;

.perfil {
    margin-top: 15rem;
    padding: 12rem 0 2rem 0;
    position: relative;
    background-color: white;
    border-radius: 1rem;

    @include m.desktop {
        border-radius: 2rem;
        background-color: v.$grisClaro;
        margin-top: 0;
        padding: 0;
    }

    &__imagen {
        width: 20rem;
        height: 20rem;
        position: absolute;
        left: 0;
        right: 0;
        top: -10rem;
        margin-left: auto; 
        margin-right: auto;
        border-radius: 1rem;
        overflow: hidden;

        @include m.desktop {
            position: static;
            margin: 2rem auto;
        }
    }

    &__nombre {
        font-family: v.$fuenteSecundaria;
        font-weight: 400;
        text-align: center;
        span {
            font-weight: 700;
        }
    }

    &__titulo {
        text-align: center;
        background-color: v.$gris;
        margin: 1rem auto;
        width: 16rem;
        padding: .7rem;
        border-radius: 1rem;
    }

    &__iconos {
        text-align: center;
        margin: 2rem 0;

        a {
            font-size: 2.5rem;
            margin: 0 1rem;
        }
        i {
            color: v.$azul;

            &:hover {
                color: v.$secundario;
            }
        }
    }

    &__informacion {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 1rem;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: center;
        }

        @include m.desktop {
            flex-direction: column;
        }
    }

    &__parrafo {
        margin: 1rem 0;

        @include m.tablet {
            margin-right: 2rem;
        }
        i {
            margin-right: 1rem;
            color: v.$azul;


        }

        a {
            color: v.$azul;
        }
    }

    &__boton {
        @include m.boton(v.$secundario, v.$blanco);
        box-shadow: -1px 1px 33px 0px rgba(138,138,138,0.49);

        i {
            margin-right: 1rem;
        }
    }
}