  body {
    background-position: 0% 0%, 100% -100%;
  }
}

.principal {
  border-radius: 3rem;
  border-top-right-radius: 0;
  margin: 0 auto;
  margin-bottom: 2rem;
  max-width: 1200px;
  width: 95%;
}
@media (min-width: 768px) {
  .principal {
    box-shadow: -1px 1px 33px 0px rgba(138, 138, 138, 0.49);
  }
}
@media (min-width: 1024px) {
  .principal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);
    background-color: #FFFFFF;
  }
}
@media (min-width: 1024px) {
  .principal__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

.perfil {
