  padding: 12rem 0 2rem 0;
  position: relative;
  background-color: white;
  border-radius: 1rem;
}
@media (min-width: 1024px) {
  .perfil {
    border-radius: 2rem;
    background-color: #f5f8f9;
    margin-top: 0;
    padding: 0;
  }
}
.perfil__imagen {
  width: 20rem;
  height: 20rem;
  position: absolute;
  left: 0;
  right: 0;
  top: -10rem;
  margin-left: auto;
  margin-right: auto;
  border-radius: 1rem;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .perfil__imagen {
    position: static;
    margin: 2rem auto;
  }
}
.perfil__nombre {
  font-family: "Noto Serif", serif;
  font-weight: 400;
  text-align: center;
}
.perfil__nombre span {
  font-weight: 700;
}
.perfil__titulo {
  text-align: center;
  background-color: #e7e7e7;
  margin: 1rem auto;
  width: 16rem;
  padding: 0.7rem;
