}
.perfil__iconos i {
  color: #0F3460;
}
.perfil__iconos i:hover {
  color: #304cfd;
}
.perfil__informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .perfil__informacion {
    flex-direction: row;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  .perfil__informacion {
    flex-direction: column;
  }
}
.perfil__parrafo {
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .perfil__parrafo {
    margin-right: 2rem;
  }
}
.perfil__parrafo i {
  margin-right: 1rem;
  color: #0F3460;
}
.perfil__parrafo a {
  color: #0F3460;
}
.perfil__boton {
  background-color: #304cfd;
  padding: 1.5rem;
  color: #FFFFFF;
  margin: 0 auto;
  font-size: 1.8rem;
  font-weight: 400;
  display: block;
  font-weight: 700;
  text-align: center;
  transition: background-color;
  transition-duration: 300ms;
  border: none;
