    width: auto;
  }
}
.perfil__boton:hover {
  background-color: #0224f8;
}
.perfil__boton i {
  margin-right: 1rem;
}

.main {
  margin-top: 2rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 1rem 2rem;
}
@media (min-width: 1024px) {
  .main {
    height: 60rem;
    overflow-y: auto;
    border-radius: 3rem;
    margin-top: 0;
    padding: 2rem;
  }
}
.main__seccion {
  margin-bottom: 3rem;
}
.main__heading {
  display: inline-block;
  position: relative;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .main__heading {
    margin-top: 0;
  }
}
.main__heading::before {
  content: "";
  background-color: #304cfd;
  height: 0.5rem;
  width: 4rem;
  display: block;
  position: absolute;
  bottom: 0;
  left: 2rem;
}
.main__heading::after {
  content: "";
  background-color: #e7e7e7;
  height: 0.5rem;
  width: 2rem;
  display: block;
}
.main__ofrecer {
  border: 1px solid #e7e7e7;
  border-radius: 1rem;
  padding: 1rem;
  margin: 2rem 0;
}
.main__ofrecer i {
  font-size: 3rem;
  padding: 1rem 0;
  color: #304cfd;
}

.copyright {
  text-align: center;
  margin: 0;
}
.copyright p {
  font-size: 1.5rem;
  margin: 0;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: #304cfd #FFFFFF;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 0rem;
}
@media (min-width: 1024px) {
  *::-webkit-scrollbar {
    width: 0.8rem;
  }
}

*::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}

*::-webkit-scrollbar-thumb {
  background-color: #304cfd;
  border-radius: 10px;
}

.burger {
  position: fixed;
  top: 1rem;
  right: 1rem;
  border: none;
  padding: 2rem;
  border-radius: 3rem;
  z-index: 2;
}
.burger__noactivo {
  background-color: #304cfd;
  transition: background-color 300ms ease-in-out;
}
.burger__noactivo i {
  color: #FFFFFF;
}
.burger__activo {
  background-color: #FFFFFF;
  border: 1px solid #e7e7e7;
  transition: background-color 300ms ease-in-out;
}
.burger__activo i {
  color: #44566c;
}
.burger i {
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  .burger {
    display: none;
  }
}

.navegacion {
  flex-direction: column;
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.navegacion__noactivo {
  opacity: 0;
  visibility: hidden;
  transition: visibility 1s, opacity 0.5s linear;
}
@media (min-width: 1024px) {
  .navegacion__noactivo {
    opacity: 1;
    visibility: visible;
    box-shadow: 11px -25px 29px -27px rgba(0, 0, 0, 0.19);
    transition: visibility 0s, opacity 0s linear;
  }
}
.navegacion__activo {
  transition: visibility 0s, opacity 0.5s linear;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 1024px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    margin-left: 40%;
    margin-top: -8rem;
    background-color: #FFFFFF;
    padding: 2rem;
    text-transform: uppercase;
    justify-content: space-around;
    border-top-left-radius: 3rem;
    border-top-right-radius: 3rem;
    position: relative;
    width: auto;
  }
  .navegacion::before {
    content: "";
    position: absolute;
    background-image: url(../img/navbar-edge.svg);
    background-size: cover;
    background-repeat: no-repeat;
    top: 0rem;
    left: -7rem;
    width: 7rem;
    height: calc(100% + 2px);
  }
}
.navegacion__burger {
  display: none;
}
.navegacion__enlace {
  color: #44566c;
  font-weight: 700;
  font-size: 1.8rem;
  padding: 1rem 2rem;
  margin: 1rem 0;
}
@media (min-width: 1024px) {
  .navegacion__enlace {
    margin: 0;
  }
}
.navegacion__enlace:hover {
  color: #304cfd;
}
.navegacion__enlace--activo {
  background-color: #304cfd;
  padding: 1rem 2rem;
  color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: -1px 1px 20px 0px rgba(138, 138, 138, 0.49);
}
.navegacion__enlace--activo:hover {
  color: #FFFFFF;
}

