@use 'base/variables' as v;
@use 'base/mixins' as m;

.portafolio {

    &__heading {
        @include m.headingBorder;
        
    }
}

.proyecto {
    border: 1px solid #e7e7e7;
    padding: 1rem;
    border-radius: 1rem;
    margin-bottom: 2rem;

    @include m.tablet {
        display: flex;
    }

    &__imagen {

        @include m.tablet {
            width: 25rem;
            max-width: 25rem;
        }
    }

    &__informacion {
        @include m.tablet {
            margin-left: 2rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    &__heading {
        position: relative;
        padding-left: 2rem;
        margin-bottom: .5rem;

        &::before {
            content: "";
            position: absolute;
            top: .8rem;
            left: 0;
            border: .2rem solid hsla(0,0%,100%,.5);
            border-radius: 2rem;
            width: 1.3rem;
            height: 1.3rem;
            background-color: #304cfd;
        }
    }

    &__parrafo {
        margin-top: 0;
    }

    &__enlace {
        color: v.$negro;

        &-icon {
            margin-right: 1rem;
        }
    }
}

.bg-image {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    height: 30rem;
    width: 30rem;
    margin: 0 auto;

    @include m.tablet {
        height: 22rem;
        width: 25rem;
    }

    &__1 {
        background-image: url(../img/proyecto1.jpg);
    }

    &__2 {
        background-image: url(../img/proyecto2.png);
        
    }
}