@use 'base/variables' as v;
@use 'base/mixins' as m;

.main {
    margin-top: 2rem;
    background-color: v.$blanco;
    border-radius: 1rem;
    padding: 1rem 2rem;

    @include m.desktop {
        height: 60rem;
        overflow-y: auto;
        border-radius: 3rem;
        margin-top: 0;
        padding: 2rem;
    }

    &__seccion {
        margin-bottom: 3rem
    }

    &__heading {
        @include m.headingBorder;
        margin-bottom: 1rem;
    }

    &__ofrecer {
        border: 1px solid v.$gris;
        border-radius: 1rem;
        padding: 1rem;
        margin: 2rem 0;

        i {
            font-size: 3rem;
            padding: 1rem 0;
            color: v.$secundario;
        }
    }
}
