@use 'base/variables' as v;

p {
    font-size: 1.5rem;
    color: v.$primario;
    line-height: 1.5;
}

h1, h2, h3, h4{
    margin: 0;
    line-height: 1.5;
    font-weight: 700;
}
h1 {
    font-size: 3rem;
}
h2 {
    font-size: 2.7rem;
}
h3 {
    font-size: 2.2rem;
}
h4 {
    font-size: 1.5rem;
}
[class$="heading"] {
    margin: 2rem 0;
}
a {
    text-decoration: none;
}
