@use 'base/variables' as v;
@use 'base/mixins' as m;

.burger {
    position: fixed;
    top: 1rem;
    right: 1rem;
    border: none;
    padding: 2rem;
    border-radius: 3rem;
    z-index: 2;

    &__noactivo {
        background-color: v.$secundario;
        transition: background-color 300ms ease-in-out;

        i {
            color: v.$blanco;
        }
    }

    &__activo {
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        transition: background-color 300ms ease-in-out;

        i {
            color: v.$primario;
        }
    }

    i {
        font-size: 2.5rem;
    }

    @include m.desktop {
        display: none;        
    }
}

.navegacion {
    flex-direction: column;
    background-color: v.$blanco;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

    &__noactivo {
        opacity: 0;
        visibility: hidden;
        transition: visibility 1s, opacity .5s linear;

        @include m.desktop {
            opacity: 1;
            visibility: visible;
            box-shadow: 11px -25px 29px -27px rgba(0,0,0,0.19);
            transition: visibility 0s, opacity 0s linear;
        }
    }

    &__activo {
        transition: visibility 0s, opacity .5s linear;
        visibility: visible;
        opacity: 1;
    }

    @include m.desktop {
        display: flex;
        flex-direction: row;
        margin-left: 40%;
        margin-top: -8rem;
        background-color: v.$blanco;
        padding: 2rem;
        text-transform: uppercase;
        justify-content: space-around;
        border-top-left-radius: 3rem;
        border-top-right-radius: 3rem;
        position: relative;
        width: auto;

        &::before {
            content: "";
            position: absolute;
            background-image: url(../img/navbar-edge.svg);
            background-size: cover;
            background-repeat: no-repeat;
            top: 0rem;
            left: -7rem;
            width: 7rem;
            height: calc(100% + 2px);
        }
    }

    &__burger {
        display: none;
    }


    &__enlace {
        color: v.$primario;
        font-weight: 700;
        font-size: 1.8rem;
        padding: 1rem 2rem;
        margin: 1rem 0;

        @include m.desktop {
            margin: 0;
        }

        &:hover {
            color: v.$secundario;
        }


        &--activo {
            background-color: v.$secundario;
            padding: 1rem 2rem;
            color: v.$blanco;
            border-radius: 1rem;
            box-shadow: -1px 1px 20px 0px rgba(138,138,138,0.49);

            &:hover {
                color: v.$blanco;
            }
        }
    }
}