@use 'base/mixins' as m;
@use 'base/variables' as v;

body {
    background-color: v.$grisClaro;
    background-image: url(../img/bg-image1.png), url(../img/bg-image2.png);
    background-repeat: no-repeat;
    background-size: 35rem, 30rem;
    background-position: 0% 0%, 100% 102%;

    @include m.desktop {
    background-position: 0% 0%, 100% -100%;
        
    }
}