@use 'base/variables' as v;
@use 'base/mixins' as m;

.principal {
    border-radius: 3rem;
    border-top-right-radius: 0;
    margin: 0 auto;
    margin-bottom: 2rem;
    max-width: 1200px;
    width: 95%;
    
    @include m.tablet {
        box-shadow: -1px 1px 33px 0px rgba(138,138,138,0.49);
    }

    @include m.desktop {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -45%);
        background-color: v.$blanco;

    }

    &__grid {
        @include m.desktop {
            display: grid;
            grid-template-columns: 1fr 2fr;
        }
    }
}