@use 'base/variables' as v;
@use 'base/mixins' as m;

.estudio {
    border: 1px solid v.$gris;
    border-radius: 1rem;
    padding: 1rem;
    margin: 2rem 0;

    &__heading {
        margin-bottom: 0;
        margin-left: 2rem;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: .3rem;
            left: -2rem;
            border: .2rem solid hsla(0,0%,100%,.5);
            border-radius: 2rem;
            width: 1.3rem;
            height: 1.3rem;
            background-color: #304cfd;
        }
    }

    &__fecha {
        margin: 0;
        color: darken(v.$grisClaro, 25%);
    }

    &__informacion {

    }
}